import React from 'react';
import styles from './Rodape.module.scss';
import LogoTJF from '../../assets/logoTJF/LogoTJF.png';


const Rodape= () => {
  return (
    <footer className={styles.rodape}>
      <img 
          src={LogoTJF}
          alt="Logo da empresa" 
        />
      <div className={styles.team}>
        <p>Cuidado, os brothers esto lhe vendo</p>
        <p>Diagnsticos e Especialistas dizem que as imagens podem lhe levar a loucura </p>
        <p>No lhe garanto quando lana essas merdas</p>
        <p>Arthur | Eduardo | Gabriel | Gustavo | Joo Vitor | Jorge Vitor</p>
        <p>Kelvi | Luiz Felipe | Luiz Henrique | Lucas Chaves | Lucas Santos </p>
        <p>Matheus | Murilo | Nathan | Wallison | Vinicius</p>
      </div>
    </footer>
  );
};

export default Rodape;
